import React from 'react';
import {Icon} from 'antd';
import Job from '../common/job'
import VideoUrl from '../assets/images/Unq91GAwTDUGM0LVnyp_231778082961_hd_hq.mp4'
import {getJobListService} from '../service/job_service'
class Conent extends React.Component{
  state={
    jobList:[]
  }
  componentDidMount(){
    this.getJobList()
   }
   getJobList=()=>{
    const res = getJobListService()
    this.setState({jobList:res})
   }
  render(){
    return <div><div className='conent'>
           <div className = 'video'>
            <video muted='muted' loop autoPlay  width='100%'>
              <source src={VideoUrl}/>
            </video>
            <div className='show'>
              <h1>此时此刻，非我莫属</h1>
              <h2>If not now,when</h2>
              <h2>If not me,who</h2>
              <div className='search'>
              <input type="text" placeholder='搜索职位或工作地点'/>
              <Icon className='icon' type="search" />
              </div>
              
            </div>
           </div>
         </div>
          <div className='bottom'>
          <div className='left'>
              <Job msg={this.state.jobList}></Job>
          </div>
          <div className='right'>
           <div className='top'> <Icon type="aliyun" style={{fontSize:'40px'}}/> &nbsp;<span>阿里云</span></div>
           <div className='bt'> <Icon type="alibaba" style={{fontSize:'30px',color:'#fd6606'}}/><span>阿里巴巴企业智能</span></div>
          </div>
    </div>
    </div>
  }
}

export default Conent